<template>
    <div class="modifyUser">
        <el-form ref="form" :model="form" label-width="80px" :rules="rules">
            <el-form-item label="username" prop="username">
                <el-col :span="24" :xs="24" :sm="11">
                    <el-input v-model="Form.username"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="gender" prop="gender">
                <el-col :span="24" :xs="24" :sm="11">
                    <el-radio v-model="Form.gender" label="1">male</el-radio>
                    <el-radio v-model="Form.gender" label="0">female</el-radio>
                </el-col>
            </el-form-item>
            <el-form-item label="birthday" prop="birthday">
                <el-col :span="24" :xs="24" :sm="11">
                    <el-date-picker type="date" placeholder="choose date" v-model="Form.birthday" style="width: 100%;"
                        format="yyyy - MM - dd" value-format="yyyy-MM-dd"></el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="favorite" prop="favorite">
                <el-col :span="24" :xs="24" :sm="11">
                    <el-checkbox-group v-model="Form.favorite">
                        <el-checkbox v-for="item in Form.favorite" :label="item" name="type"></el-checkbox>
                        <el-popover placement="top" width="160" v-model="visible">
                            <el-input type="text" v-model="newFavor" placeholder="input your favorite"></el-input>
                            <div style="text-align: right; margin: 0">
                                <el-button size="mini" type="text" @click="visible = false">cancle</el-button>
                                <el-button type="primary" size="mini" @click="addNewFavor">confirm</el-button>
                            </div>
                            <el-button slot="reference" size="mini">ADD</el-button>
                        </el-popover>
                    </el-checkbox-group>
                </el-col>
            </el-form-item>
            <el-form-item label="quote" prop="quote">
                <el-col :span="24" :xs="24" :sm="11">
                    <el-input type="textarea" v-model="Form.quote" resize></el-input>
                </el-col>
            </el-form-item>
            <el-form-item>
                <el-col :span="24" :xs="24" :sm="11">
                    <el-button type="primary" @click="onSubmit">modify</el-button>
                    <el-button @click="onReset">cancel</el-button>
                </el-col>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            Form: {},
            rules: {
                username: [
                    { required: true, message: 'invalid username', trigger: 'blur' },
                    { min: 3, message: 'between 3-5', trigger: 'blur' }
                ],
                gender: [
                    { required: true, message: 'select your gender', trigger: 'change' }
                ],
                birthday: [
                    { type: 'date', required: true, message: 'invalid date', trigger: 'blur' }
                ],
                favorite: [
                    { type: 'array', required: true, message: 'input your favorite ', trigger: 'change' }
                ],
                quote: [
                    { required: true, message: 'input your quote', trigger: 'blur' }
                ]
            },
            newFavor: '',
            visible: false
        }
    },
    mounted() {
        this.getUser();
    },
    methods: {
        onSubmit() {
            this.$store.commit('modifyUserInfo', this.Form);
            // console.log(this.form);
            this.$store.commit('toModifyPage');
            this.$message({
                message: 'modify successful!',
                type: 'success'
            });

        },
        onReset() {
            this.$store.commit('toModifyPage');
        },
        addNewFavor() {
            if (this.newFavor !== '') {
                this.Form.favorite.push(this.newFavor);
                this.newFavor = '';
                this.onSubmit();
                this.visible = !this.visible;
            } else {
                this.$message.error('invalid input');
            }
        },
        getUser(){
            // 解决同步修改了页面上的数据
            this.Form = JSON.parse(JSON.stringify(this.form))
        }
    },
    computed: {
        form() {
            return this.$store.getters.getUserInfo;
        }
    }
}
</script>