<template>
    <div class="userInfo">
        <el-descriptions class="margin-top" :column="1" :size="size" border>
            <template slot="extra">
                <el-link type="primary" @click="handletoModify">
                    modify
                    <i class="el-icon-edit"></i>
                </el-link>
            </template>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-bank-card"></i>
                    username
                </template>
                {{ form.username }}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    gender
                </template>
                {{ form.gender == 1 ? 'male' : 'female' }}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-date"></i>
                    birthday
                </template>
                {{ form.birthday }}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-football"></i>
                    favorite
                </template>
                <el-tag v-for="item in form.favorite">{{ item }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-data-line"></i>
                    quote
                </template>
                {{ form.quote }}
            </el-descriptions-item>
        </el-descriptions>
    </div>
</template>
<script>
export default {
    data() {
        return {
            size:'middle'
        }
    },
    methods: {
        handletoModify(){
            this.$store.commit('toModifyPage');
        }
    },
    computed: {
        form() {
            return this.$store.getters.getUserInfo;
        }
    }
}
</script>